.projectDetailPage{background-color:#fff;color:#000;min-height:100%;display:flex;flex-direction:column}.container{padding:1rem;height:100%;display:flex;flex-direction:column;gap:1rem;flex:1}.imagesContainer{display:grid;grid-template-columns:repeat(3, 1fr);flex:3;position:relative;overflow:hidden}.infoContainer{display:flex;flex-direction:row;gap:1rem;justify-content:space-between;flex:2}.firstBox{flex:1;display:flex;flex-direction:column;gap:1rem}.firstBoxUpperRow{display:flex;flex-direction:row;gap:1rem;flex:1}.firstBoxLowerRow{display:grid;flex-direction:row;gap:1rem;flex:2;background-color:#fff;border:1px solid #000;border-radius:3rem;padding:1rem;grid-template-columns:repeat(2, 1fr);grid-template-rows:repeat(2, 1fr)}.firstBoxDecoration{background-color:#fff;border:1px solid #000;border-radius:3rem;padding:1rem;display:flex;flex-direction:column;justify-content:center;align-items:center;flex:2}.firstBoxChat{flex:1;display:flex;flex-direction:column;justify-content:center;align-items:center;background-color:#fff;border:1px solid #000;border-radius:2rem}.secondBox,.thirdBox{background-color:#fff;border:1px solid #000;border-radius:2rem;padding:1rem;flex:1}.loading,.notFound{display:flex;flex-direction:column;justify-content:center;align-items:center;height:100%;font-family:var(--font-geist-sans)}.notFound h1{margin-bottom:1rem}